header{
 position: fixed;
 z-index: 999;
 top: 0;
 left: 0;
 right: 0;
 height: 70px;
 display: flex;
 justify-content: space-between;
 align-items: center;
 padding: 0 30px;
 background:transparent;
}
.black{
    background-color: #141414;
    transition: all 0.5s;
}
/* img{
    width: 100px;
    height: 100px; */

.header--logo{
    height: 30px;
}
.header--logo img{
    height: 100%;
}
.header--user{
    height: 40px;
}
.header--user img{
    height: 100%;
    border-radius: 4px;
}