.movieRow{
    margin-bottom: 30px;
}
.movieRow h2{
    margin: 0;
    margin-left: 30px;
    text-transform: capitalize ;
}
.movieRow--listArea{
    overflow-x: hidden;

}
.movieRow--list{
    transition: all ease 0.5s;
}
.movieRow--list{
    cursor: pointer;
}

.movieRow--item{
    display: inline-block;
    width: 170px;
}
.movieRow--item img{
    display: inline-block;
    width: 100%;
    transform:scale(0.9);
    transition: all ease 0.3s;
}
.movieRow--item img:hover{
    transform:scale(1);
}
.movieRow--right,.movieRow--left{
    position: absolute;
    width: 40px;
    height: 260px;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.6);    
    opacity: 0;
}
.movieRow--left{
    left: 0;
}
.movieRow--right{
    right: 0;
}
.movieRow:hover .movieRow--right, .movieRow:hover .movieRow--left{
    opacity: 1;
    transition:all ease 0.5;
}

@media (max-width:760px) {
    .movieRow--right,.movieRow--left{
        opacity: 1;
    }
}
.featured{
    height: 100vh;
}
.featured--vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    justify-content: center;
    display: flex;
    flex-direction: column;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 30px;
    
}
.featured--name{
    font-size: 5vmax;
    font-weight: bold;
}
.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}
.featured--info > *{
    display: inline-block;
    margin-right: 15px;
}
.featured--points{
    color:#43d369;
}
.featured--overview{
margin-top: 15px;
color: #999;
max-width: 40%;
}
.featured--buttons {
    margin-top: 15px;
    text-transform: capitalize;
}
.featured--buttons > *{
display:inline-block ;
border-radius: 5px;
font-size: 20px;
font-weight: bold;
padding: 11px 25px;
margin-right: 15px;
text-decoration: none;
opacity: 1;
}
.featured--buttons>*:hover{
    transition: all ease 0.2s;
    opacity: 0.7;
}
.featured--buttonWatch,.featured--buttonWatch:hover{
    color: #fff;
    background-color: #333;
}
.featured--buttonList,.featured--buttonList:hover{
    color: #111;
    background-color: #fff;
}
.featured--genre{
    color: #999;
    margin-top: 15px;
    margin-bottom: 15px;
    font-size: 18px;
}

@media (max-width:760px) {
  .featured{
    height: 90vh;
  }
  .featured--horizontal {

    background: linear-gradient(to bottom, #111 20%, transparent 80%);
     opacity:0.8;
  }
  .featured--name{
      margin-top: 10px;
  }
  .featured--info{
      font-size: 16px;
  }
  .featured--overview{
      font-size: 14px;
      max-width: 100%;
      margin-right: 30px;
      color: #fff;
  }
  .featured--genre{
      font-size: 16px;
  }
  .featured--buttons >*{
      font-size: 16px;
      padding: 10px 12px;
  }
}
