/* effect underline */
.underlineEffects ul { 
  margin: 0 auto; 
  padding: 0; 
  list-style: none; 
  display: table;
  text-align: center;
}
.underlineEffects li { 
  display: table-cell; 
  position: relative; 
  padding: 15px 0;
}
.underlineEffects a, .underlineEffectsFooter a{
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.15em;
  display: inline-block;
  padding: 15px 20px;
  position: relative;
}
.underlineEffects a:after ,.underlineEffectsFooter a::after {    
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: #fff;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

.underlineEffects a:hover:after, .underlineEffectsFooter a:hover:after { 
  width: 100%; 
  left: 0; 
}
